import React, { useState } from "react";
import { BrowserRouter, Link } from "react-router-dom";
import "./Navbar.css";

function Navbar() {
  const [click, setClick] = useState(false);
  // const [NavBtnState, setNavBtnState] = useState([false, false, false]);

  const closeMenu = () => {
    setClick(false);
  };

  // const navClick = (btnIndex: number) => {
  //   let tempBtns = NavBtnState;
  //   if (NavBtnState.includes(true)) {
  //     if (NavBtnState[btnIndex] == true) {
  //       return;
  //     }
  //     let index = NavBtnState.findIndex((item) => item == true);
  //     tempBtns[index] = false;
  //     tempBtns[btnIndex] = true;
  //   } else {
  //     tempBtns[btnIndex] = !NavBtnState[btnIndex];
  //   }
  //   return setNavBtnState(tempBtns);
  // };

  return (
    <nav className="navbar">
      <div className="navbar-container">
        <Link to="/MUAS/" className="navbar-logo">
          MUAS
        </Link>
        <div className="menu-icon" onClick={() => setClick((prev) => !prev)}>
          <i className={click ? "fas fa-times" : "fas fa-bars"} />
        </div>
        <ul className={click ? "nav-menu active" : "nav-menu"}>
          <li className="nav-item">
            <Link to="/MUAS/" className="nav-links" onClick={closeMenu}>
              Home
            </Link>
          </li>
          <li className="nav-item">
            <Link
              to="/MUAS/committee"
              className="nav-links"
              onClick={closeMenu}
            >
              Committee
            </Link>
          </li>
          {/* <li className="nav-item">
            <Link to="/MUAS/events" className="nav-links" onClick={closeMenu}>
              Events
            </Link>
          </li> */}
          <li className="nav-item">
            <Link to="/MUAS/mudae" className="nav-links" onClick={closeMenu}>
              MUDAE
            </Link>
          </li>
          <li className="nav-item">
            <div className="nav-link-discord">
              <a
                href="https://discord.gg/pYcSm5ZBqS"
                className="discord-btn"
                onClick={closeMenu}
                target="_blank"
              >
                <span>Join Our Discord</span>
              </a>
            </div>
          </li>
        </ul>
      </div>
    </nav>
  );
}

export default Navbar;
